import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { Product, Category } from '../../interfaces/Product';
import { getProducts } from '../../services/CatalogService';
import ListItemProduct from '../../components/ListItemProduct/ListItemProduct';
import ListGroup from '../../components/ListGroup/ListGroup';
import TabContainer from '../../components/Tab/TabContainer';
import Tab from '../../components/Tab/Tab'; 

export default () => {
    const [categories, setCategories] = useState<Category[]>([]);
    const [products, setProducts] = useState<Product[]>([]);
    const { id } = useParams();

    useEffect(() => {
        getProducts(id, (result: Product[]) => {
            setProducts(result);
            const productCategories: Category[] = [];
            result.forEach(product => {
                if (productCategories.map(category => category.id).indexOf(product.category.id) === -1) {
                    productCategories.push(product.category);
                }
            })
            setCategories(productCategories);
        });
    }, [id]);



    return <div className='m-l--20 m-r--20 m-t--31 '>
        <TabContainer>
            {
                categories.map((category: Category, index: number) => <Tab active={index === 0} id={category.id} label={category.name}>
                    <ListGroup>
                        {
                            products
                                .filter((product: Product) => product.category.id === category.id)
                                .map((product: Product) => (
                                    <ListItemProduct key={product.id} product={product} />
                                ))
                        }
                    </ListGroup>
                </Tab>)
            }
        </TabContainer>
    </div>
}