import React, { useEffect, useState } from 'react';
import firebase from 'firebase';

import './App.css';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom';

import Navbar from './components/Navbar/Navbar';
import Home from './scenes/Home/Home';
import Sidebar from './components/Sidebar/Sidebar';
import Favorites from './scenes/Favorites/Favorites';
import Catalog from './scenes/Catalog/Catalog';
import Checkout from './scenes/Checkout/Checkout';
import { Emitter } from './services/EventEmmiter';
import { Cart } from './interfaces/Cart';
import { Constants } from './interfaces/Constants';
import SellerRecommendation from './scenes/SellerRecommendation/SellerRecommendation';

export default () => {
  const [minicart, setMinicart] = useState<Cart>({ itens: [] });

  useEffect(() => {
    setMinicart(JSON.parse(localStorage.getItem(Constants.MINICART_LOCAL_STORAGE_KEY) || "{ \"itens\": [] }"))

    const firebaseConfig = {
      apiKey: "AIzaSyDDQljAKKIMXFmHN96hJl9FAP3-rCEYfc4",
      authDomain: "meudelivery-p.firebaseapp.com",
      databaseURL: "https://meudelivery-p.firebaseio.com",
      projectId: "meudelivery-p",
      storageBucket: "meudelivery-p.appspot.com",
      messagingSenderId: "739052945087",
      appId: "1:739052945087:web:1d7cc071fc738822a4d00a",
      measurementId: "G-BXW0K884BJ"
    };

    firebase.initializeApp(firebaseConfig);

    firebase.auth()
      .signInAnonymously()
      .catch(err => console.error(err))

    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        if (user.isAnonymous) {
          localStorage.setItem('USER_ID', user.uid);
        }
      } else {
        console.log("Not user received from firebase");
      }
    });

    Emitter().addListener(Constants.EVENT_REFRESH_MINICART, () => {
      setMinicart(JSON.parse(localStorage.getItem(Constants.MINICART_LOCAL_STORAGE_KEY) || "{}"))
    });
  }, [])

  return (
    <Router>
      <Navbar />
      <Sidebar />

      <section className="content">
        <div className="container-fluid">
          <Switch>
            <Route path="/" exact><Home /></Route>
            <Route path="/favoritos"><Favorites /></Route>
            <Route path="/catalog/:id"><Catalog /></Route>
            <Route path="/checkout"><Checkout /></Route>
            <Route path="/sellerRecommendation"><SellerRecommendation /></Route>
          </Switch>
        </div>
      </section>

      {(minicart.itens && minicart.itens.length > 0) && <Link className="mini-cart-footer" to='/checkout'>
        <span>Você tem {minicart.itens.length > 0 ? minicart.itens.map(item => item.quantity).reduce((previous, current) => previous + current) : 0 } produtos no carrinho. <b>Clique Aqui</b> para finalizar o pedido.</span>
      </Link>}
    </Router>
  );
}
