import React from 'react';
import { Cart } from '../../interfaces/Cart';
import { Constants } from '../../interfaces/Constants';
import { OrderItem } from '../../interfaces/OrderItem';
import { Product } from '../../interfaces/Product';
import { emit } from '../../services/EventEmmiter';
import ListItem from '../ListItem/ListItem';
import './ListItemProduct.css';


interface ListItemProductProps {
    product: Product
}

export default ({ product }: ListItemProductProps) => {

    const maskPrice = product.price ? `R$ ${(product.price / 100).toFixed(2).replace('.', ',')}` : ''
    
    const addProduct = () => {
        const seller = localStorage.getItem(Constants.SELECTED_SELLER_LOCAL_STORAGE_KEY);
        if (!seller || seller === product.sellerId) {
            const cart: Cart = JSON.parse(localStorage.getItem(Constants.MINICART_LOCAL_STORAGE_KEY) || '{}');
    
            if (!cart.itens) {
                cart.itens = [];
            }
    
            const item = cart.itens.find(orderItem => product.id === orderItem.product.id);
            if (item) {
                item.quantity += 1;
            } else {
                cart.itens.push({
                    product,
                    quantity: 1
                } as OrderItem);
            }
    
            localStorage.setItem(Constants.MINICART_LOCAL_STORAGE_KEY, JSON.stringify(cart));
            localStorage.setItem(Constants.SELECTED_SELLER_LOCAL_STORAGE_KEY, product.sellerId);
            emit(Constants.EVENT_REFRESH_MINICART);
        } else {
            alert('Por enquanto você só pode adicionar produtos de um mesmo estabelecimento no seu carrinho.');
        }
    }

    return <ListItem>
            <div onClick={() => addProduct()} className="row list-item-product__product">
                <div className='col-xs-8 col-md-10 col-lg-11'>
                    <h2 className='m-t-5 font-20 font-bold'>{product.name}</h2>
                    <small>{product.description}</small>
                </div>
                <div className="col-xs-4 col-md-2 col-lg-1">
                    <small className='font-bold pull-right'>{maskPrice}</small>
                    <img alt='product' className='img-responsive rounded float-right' src={product.image} />
                </div>
            </div>
        </ListItem>
}