import React from 'react'

interface TabProps {
    id: string;
    active: boolean;
    label: string;
    children: React.ReactNode
}

export default ({ id, active, children }: TabProps) => <div role="tabpanel" className={`tab-pane fade ${active ? 'in active' : ''}`} id={id.toString()}>
    {children}
</div>