import React, { ReactElement } from 'react'
import Card from '../Card/Card'

import './TabContainer.css'

interface TabContainerProps {
    children: ReactElement[];
}

interface TabHeaderProps {
    active: boolean;
    openId: number;
    label: string;
}

const TabHeader = ({ active, openId, label }: TabHeaderProps) => (
    <li role="presentation" className={`${active ? 'active' : ''}`}><a href={`#${openId}`} data-toggle="tab">{label}</a></li>
)

export default ({ children }: TabContainerProps) => {

    return <Card>
        <div className="tab-header">
            <ul className="nav nav-tabs tab-nav-right" role="tablist">
                {React.Children.map(children, (child, index) => <TabHeader key={index} active={child?.props.active} openId={child?.props.id} label={child.props.label} />)}
            </ul>
        </div>
        <div className="tab-content">
            {children}
        </div>
    </Card>
}