import React, { useState } from 'react';
import Card from '../../components/Card/Card';
import Input from '../../components/Input/Input';

import firebase from 'firebase';

export default () => { 
    const [seller, setSeller] = useState<any>({});
 
    const handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
        seller[target.name] = target.value;
        setSeller(seller);
    }

    const send = () => {
        firebase.firestore().collection('seller_recommendation')
            .doc()
            .set(seller)
            .then(res => {
                setSeller({}); 
                alert('Obrigado pela recomendação :D');
                window.location.href = '/';
            });            
    }
  
    return <Card title="Indicar um Estabelecimento">
        <div className="body">
            <div className="row">
                <div className="col-xs-12">
                    <Input
                        label='Nome'
                        name='name'
                        initialData={seller || {}}
                        onChange={handleChange}
                    />
                </div>
                <div className="col-xs-12">
                    <Input
                        label='Telefone'
                        name='phone'
                        initialData={seller || {}}
                        onChange={handleChange}
                    />
                </div>
                <div className="col-xs-12">
                    <Input
                        label='Observações'
                        name='obs'
                        initialData={seller || {}}
                        onChange={handleChange}
                    />
                </div>
                <div className="col-xs-12">
                    <button className='btn btn-primary btn-block' onClick={send}>Enviar</button>
                </div>
            </div>
        </div>
    </Card>
}