import React, { useEffect, useState } from 'react';
import Card from '../../components/Card/Card';
import Input from '../../components/Input/Input';
import { Constants } from '../../interfaces/Constants';
import { OrderItem } from '../../interfaces/OrderItem';
import { Seller } from '../../interfaces/Seller';
import { emit } from '../../services/EventEmmiter';
import { getSellerById } from '../../services/SellersService';

export default () => {
    const [seller, setSeller] = useState<Seller>();
    const [refreshCheckout, setRefreshCheckout] = useState<number>(1);
    const [order, setOrder] = useState<any>({ address: {} });

    useEffect(() => {
        const sellerId: string = localStorage.getItem(Constants.SELECTED_SELLER_LOCAL_STORAGE_KEY) || '';
        getSellerById(sellerId, seller => setSeller(seller));

        const address = JSON.parse(localStorage.getItem(Constants.ADDRESS_LOCAL_STORAGE_KEY) || "{}");
        const savedOrder = JSON.parse(localStorage.getItem(Constants.MINICART_LOCAL_STORAGE_KEY) || "{}");
        if (!savedOrder.address) {
            savedOrder.address = address;
        }

        setOrder(savedOrder);
    }, [refreshCheckout]);

    const handleChange = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
        order[target.name] = target.value;
        updateOrder(order);
    }

    const handleAddressChange = ({ target }: React.ChangeEvent<HTMLInputElement>) => {
        if (!order.address) {
            order.address = {};
        }

        order.address[target.name] = target.value;
        updateOrder(order);
    }

    const updateOrder = (newOrder: any) => {
        if (newOrder.itens.length === 0) {
            localStorage.removeItem(Constants.SELECTED_SELLER_LOCAL_STORAGE_KEY);
        }

        setOrder(newOrder);
        localStorage.setItem(Constants.MINICART_LOCAL_STORAGE_KEY, JSON.stringify(newOrder));
        emit(Constants.EVENT_REFRESH_MINICART);
        setRefreshCheckout(refreshCheckout + 1);
    }

    const addQuantity = (item: OrderItem) => {
        const cardItem: OrderItem = order.itens.find((orderItem: OrderItem) => item.product.id === orderItem.product.id);
        cardItem.quantity += 1;
        updateOrder(order);
    }

    const removeQuantity = (item: OrderItem) => {
        const cardItem: OrderItem = order.itens.find((orderItem: OrderItem) => item.product.id === orderItem.product.id);
        cardItem.quantity -= 1;

        if (cardItem.quantity <= 0) {
            const index = order.itens.indexOf(cardItem);
            order.itens.splice(index, 1);
        }

        updateOrder(order);
    }

    const finishOrder = () => {
        if (!order.paymentType) {
            alert('Escolha um Modo de Pagamento');
        } else {
            if (!order.address) {
                alert('Escolha um Endereço de Entrega');
            } else {
                const message: string = createMessage();
                window.open(`https://api.whatsapp.com/send?phone=${seller?.whatsapp}&text=${window.encodeURIComponent(message)}`, '_blank')

                localStorage.setItem(Constants.ADDRESS_LOCAL_STORAGE_KEY, JSON.stringify(order.address));

                localStorage.removeItem(Constants.SELECTED_SELLER_LOCAL_STORAGE_KEY);
                localStorage.removeItem(Constants.MINICART_LOCAL_STORAGE_KEY);
                emit(Constants.EVENT_REFRESH_MINICART);
                return window.location.href = '/'
            }
        }
    }

    const createMessage = (): string => {
        if (seller) {
            let message = "*Pedido do DDelivery*\n\n";
            order.itens.forEach((item: OrderItem) => message += `${item.quantity} ${item.product.name} - ${getPrice(item)}\n`);
            message += `\n*Taxa de Entrega:* ${((seller.deliveryPrice ? seller.deliveryPrice : 0) / 100).toFixed(2)}`
            message += `\n*Entregar em:* ${order.address.street} ${order.address.addressNumber} - ${order.address.neighborhood}. ${order.address.city}`;
            message += `\n*Pagamento em:* ${order.paymentType}`
            message += `\n*Valor Total Com Taxa de Entrega:* ${getTotalPrice(seller, order.itens)}`;

            return message;
        }
        return '';
    }

    const getTotalPrice = (seller: Seller, items: OrderItem[]) => {
        let totalPrice: number = seller.deliveryPrice || 0;
        items
            .forEach(item => {
                if (item.product.price)
                    totalPrice += (item.quantity * item.product.price)
            });

        return `R$ ${(totalPrice / 100).toFixed(2)}`;
    }

    const getPrice = (item: OrderItem): string => {
        if (item.product.price) return `R$ ${(item.product.price / 100).toFixed(2)} - R$ ${(item.quantity * (item.product.price / 100)).toFixed(2)}`;
        return '';
    }

    return <div className='row remove-margin'>
        <div className='col-xs-12 col-md-offset-2 col-md-8 col-lg-offset-3 col-lg-6'>

            <Card title="Carrinho" subtitle="">
                <div className="body">
                    <table className='table'>
                        <thead>
                            <tr>
                                <th>Produto</th>
                                <th>Valor</th>
                                <th>#</th>
                            </tr>
                        </thead>
                        <tbody>
                            {order.itens && order.itens.map((item: OrderItem) => <tr key={item.product.id}>
                                <td><span className='font-bold'>{item.quantity}</span> - {item.product.name}</td>
                                <td>{(item.quantity * ((item.product.price || 0) / 100)).toFixed(2).replace('.', ',')}</td>
                                <td style={{ width: '95px' }}>
                                    <div className='btn-group' role='group'>
                                        <button className='btn btn-sm btn-primary' onClick={() => addQuantity(item)}><i className='fa fa-plus' /></button>
                                        <button className='btn btn-sm btn-danger' onClick={() => removeQuantity(item)}><i className='fa fa-minus' /></button>
                                    </div>
                                </td>
                            </tr>)}
                            <tr>
                                <td><span className="font-bold">Total Dos Produtos</span></td>
                                <td>
                                    {order.itens &&
                                        order.itens
                                            .map((item: OrderItem) => item.quantity * (item.product.price || 0) / 100)
                                            .reduce((previous: number, current: number) => previous + current)
                                            .toFixed(2)
                                            .replace('.', ',')
                                    }
                                </td>
                                <td></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </Card>
            <Card title="Entrega" subtitle="">
                <div className="body">
                    <h4 className='m-b-20'>Qual o endereço de entrega?</h4>

                    <div className="row">
                        <div className="col-xs-12">
                            <Input
                                label='Rua'
                                name='street'
                                initialData={order.address}
                                onChange={handleAddressChange}
                            />
                        </div>
                        <div className="col-xs-12">
                            <Input
                                label='Número'
                                name='addressNumber'
                                initialData={order.address}
                                onChange={handleAddressChange}
                            />
                        </div>
                        <div className="col-xs-12">
                            <Input
                                label='Bairro'
                                name='neighborhood'
                                initialData={order.address}
                                onChange={handleAddressChange}
                            />
                        </div>
                        <div className="col-xs-12">
                            <Input
                                label='Cidade'
                                name='city'
                                initialData={order.address}
                                onChange={handleAddressChange}
                            />
                        </div>
                    </div>
                </div>
            </Card>
            <Card title="Pagamento" subtitle="">
                <div className="body">
                    <h4>Como será realizado o pagamento?</h4>

                    <div className="row m-t-20">
                        {
                            (seller && seller.paymentMethods) && seller.paymentMethods.map((paymentMethod: string, index: number) => <div className="col-xs-12 col-md-4 col-lg-2">
                                <input type="radio" id={`paymentType-${index}`} name="paymentType" value={paymentMethod} onChange={handleChange} />
                                <label htmlFor={`paymentType-${index}`}>{paymentMethod}</label>
                            </div>)
                        }
                    </div>

                    <button className='btn btn-block bg-blue' onClick={() => finishOrder()}>Finalizar o Pedido</button>
                </div>
            </Card>
        </div>
    </div>
}