import React, { useEffect, useState } from 'react'

interface InputProps {
    label: string;
    type?: string;
    name: string;
    initialData: any;
    onChange: Function;
}

export default ({ label, type = 'text', name, initialData, onChange }: InputProps) => {
    const [styleClass, setStyleClass] = useState('form-line')

    useEffect(
        () => {
            if(initialData[name])
                onFocus()
        },
        [initialData]
    )

    const onFocus = () => {
        setStyleClass('form-line focused')
    }

    const onBlur = ({target}: React.ChangeEvent<HTMLInputElement>) => {
        if(!target.value.trim())
            setStyleClass('form-line')
    }

    return <div className="form-group form-float">
        <div className={styleClass}>
            <input type={type} className="form-control" name={name} onFocus={onFocus} onBlur={onBlur} value={initialData[name]} onChange={e => onChange(e)} />
            <label className='form-label'>{label}</label>
        </div>
    </div>
}
