import React from 'react'

interface CardProps {
    title?: string,
    subtitle?: string,
    children: React.ReactNode
}

export default ({ title, subtitle, children }: CardProps) => {

    const header = (title && <div className="header bg-blue">
        <h2>
            {title}
            <small>{subtitle}</small>
        </h2>
    </div>)

    return <div className="card">
        {header}
        {children}
    </div>
}